/* media breakpoint mixin */
#wpadminbar {
  display: none !important;
}

html.wp-toolbar {
  padding-top: 0px !important;
}

#adminmenu, #adminmenu .wp-submenu, #adminmenuback, #adminmenuwrap {
  width: 210px;
  background: #E5E5E5;
}

#adminmenu li.menu-top .wp-submenu {
  z-index: 99999;
}

#adminmenu li.menu-top .wp-submenu a,
#adminmenu li.menu-top .wp-submenu li a.current,
#adminmenu li.menu-top .wp-submenu a:hover,
#adminmenu li.menu-top a.menu-top {
  font-size: 14px;
  font-weight: 700;
  color: #000;
  transition-timing-function: ease-in-out;
  transition-duration: 300ms;
  transition-property: all;
}

@media (any-hover: hover) {
  #adminmenu li.menu-top .wp-submenu a:hover,
  #adminmenu li.menu-top .wp-submenu li a.current:hover,
  #adminmenu li.menu-top .wp-submenu a:hover:hover,
  #adminmenu li.menu-top a.menu-top:hover {
    color: #EEB114;
  }
}

#adminmenu li.current a.menu-top,
#adminmenu li.wp-has-current-submenu a.wp-has-current-submenu {
  background: rgba(255, 255, 255, 0);
  color: #EEB114;
}

#adminmenu .wp-has-current-submenu ul > li > a {
  padding-left: 50px;
}

#collapse-button:hover {
  color: #EEB114;
}

#wpcontent, #wpfooter {
  margin-left: 210px;
}
